* {
    box-sizing: content-box;
}


body {
    margin : 0;
    padding : 0;
    font-family: 'Lucida Sans Regular';
    background-color: black;
}

html,
body,
#root,
.container {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
}

.loader {
    display: flex;
    align-items: center;
}

.loader__text{
    font-size: 25px;
    font-weight: 600;
    color: white;
}

.movies {
    padding: 70px 50px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 80%;
}

.movie {
    padding: 20px;
    font-weight: 300;
    color: rgb(170, 169, 169);
    display: flex;
    
    align-items: flex-start;
    background-color: white;
    margin-bottom: 70px;
    width: 40%;
    border-radius: 6px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    
}

.movie img {
    max-width: 150px;
    margin-right: 30px;
    position: relative;
    bottom: 40px;
    border-radius: 5px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie__title,
.movie__year,
.movie__genres,
.movie__summary {
    margin: 0;
}

.movie__title {
    color: rgb(68, 67, 67);
    font-weight: 600;
    font-size: 20px;
    margin: 10px 0px;
}

.movie__year {
    margin-bottom: 5px;
}

.movie__genres {
    color: rgb(104, 103, 103);
    font-weight: 500;
    list-style: none;
    padding : 0;
    font-size: 10px;
    display: flex;
    margin-bottom: 15px;
}
.genres__genre {
    margin-right: 10px;
}

.movie__summary {
    font-size: 15px;
}